.drawer_card{
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  width: 100%;
  background: #FFFFFF;
  border: 1px solid #F3F3F3;
  border-radius: 20px;
  padding: 20px 0px 20px 20px;
}

.sneaker_title{
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
}

.sneaker__info{
  max-width: 170px;
  padding-left: 21px;
}

.price{
  padding-top: 8px;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
}

.btn_deny{
  width: 32px;
  height: 32px;
  border: 1px solid #DBDBDB;
  border-radius: 8px;
  margin-left: 10px;
  padding: 5px 10px;
  cursor: pointer;
}