.favorite__button{
  position: absolute;
  width: 32px;
  height: 32px;
  background-color: #FFFFFF;
  border: 1px solid #F8F8F8;
  border-radius: 7px;
  cursor: pointer;
  img{
    margin-top: 3px;
  }
}

.favorite__button_active{
  position: absolute;
  width: 32px;
  height: 32px;
  background-color: #FEF0F0;
  border: none;
  border-radius: 7px;
  cursor: pointer;
  img{
    margin-top: 3px;
  }
}